<template>
    <div class="header">
        <h2>Organization Manager</h2>
    </div>
    <hr>
    <div class="body">
        <div class="actions">
           <div class="row">
            <div class="col-sm-1">
                <label for="filter">Filter by</label>
            </div>
            <div class="col-1">
               <select name="" id="" class="form-select-sm">
                      <option value="">All</option>
                      <option value="">Active</option>
                      <option value="">Inactive</option>
               </select>
            </div>
            <div class="col-4"></div>
            <div class="col-3">
                <input type="text" placeholder="Search ..." class="form-control">
            </div>
            <div class="col">
              <p class="action-button">
                <button class="btn btn-outline-primary " v-bind:title="'Create'" data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                        <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                        <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
                      </svg>
                      Create
                </button>
                <button class="btn btn-outline-danger " v-bind:title="'Delete'">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x-fill" viewBox="0 0 16 16">
                        <path fill-rule="evenodd" d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm6.146-2.854a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                      </svg>
                      Remove
                </button>
              </p>
            </div>
            <hr>
            <div class="table-responsive-sm">
                <table class="table table-striped table-bordered table-hover">
                    <thead>
                        <th><input type="checkbox" @change="checkAll" id="check"></th>
                        <th>Code</th>
                        <th>Name</th>
                        <th>Orders</th>
                        <th>Description</th>
                        <th>ParentName</th>
                        <th>User of Organization</th>
                        <th>Action</th>
                    </thead>
                    <tbody>
                       <tr class="col" v-for="item in dataList" :key="item">
                        <td class="col">
                            <input type="checkbox" class="check">
                        </td>
                        <th class="col">
                            {{item.code}}
                        </th>
                        <td class="col">
                            {{item.name}}
                        </td>
                        <td class="col">
                            {{item.orders}}
                        </td>
                        <td class="col">
                            {{item.description == '' ? 'N/A' : item.description}}
                        </td>
                        <td class="col">
                            {{item.organization == null ? 'N/A' : item.organization.name}}
                        </td>
                        <td class="col">
                            <span @click.prevent="objDetail= item" class="detail badge text-bg-success" data-bs-toggle="modal" data-bs-target="#modal-detail">chi tiết</span>
                        </td>
                        <td>
                            <span>
                                <button @click="objectUpdate = item" class="btn btn-outline-primary" v-bind:title="'Edit'" data-bs-toggle="modal" data-bs-target="#update-modal" id="close2">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pen-fill" viewBox="0 0 16 16">
                                        <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001z"/>
                                    </svg>
                                </button>
                                <button data-bs-toggle="modal" data-bs-target="#confirm" class="btn btn-outline-danger" v-bind:title="'Delete'" @click.prevent="id = item.oraId">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash3" viewBox="0 0 16 16">
                                        <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"/>
                                    </svg>
                                </button>
                            </span>
                        </td>
                       </tr>
                    </tbody>
                </table>
            </div>
           </div>
        </div>
        <div class="table table-responsive">

        </div>
    </div>
    <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="staticBackdropLabel">Thêm mới</h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <form @submit.prevent="create">
                <div class="form-floating mb-3">
                    <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="organization.code" required>
                    <label for="floatingInput">Code</label>
                </div>
                <div class="form-floating mb-3">
                    <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="organization.name" required>
                    <label for="floatingInput">Name</label>
                </div>
                <div class="form-floating mb-3" >
                    <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="organization.orders" required>
                    <label for="floatingInput">Orders</label>
                </div>
                <div class="form-floating mb-3">
                    <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="organization.description">
                    <label for="floatingInput">Description</label>
                </div>
                <div class="form-floating mb-3">
                    <select class="form-select" id="floatingSelect" aria-label="Floating label select example" v-model="organization.organization.oraId">
                      <option selected disabled>Choose parent</option>
                      <option v-for="item in dataList" :key="item" v-bind:value="item.oraId">{{item.name}}</option>
                    </select>
                    <label for="floatingSelect">Parent</label>
                  </div>
                <div class="row">
                    <div class="col-3 m-0 p-0">
                        <button class="btn btn-outline-success flex-lg-column" >Thêm mới</button>
                    </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close">Đóng</button>
            </div>
          </div>
        </div>
      </div>  
      <!-- Button trigger modal -->
  
  <!-- Modal -->
  <div class="modal fade" id="confirm" tabindex="-1" aria-labelledby="confirm" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Xác nhận</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
         Bạn có chắc muốn xóa bản ghi này không ? 
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close1">Không</button>
          <button class="btn btn-primary"  @click.prevent="remove(id)">Có</button>
        </div>
      </div>
    </div>
  </div>
  <!-- Detail Update -->
  <div class="modal fade" id="update-modal" tabindex="-1" aria-labelledby="update-modal" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Cập nhật</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="updateById(objectUpdate.oraId)">
            <div class="form-floating mb-3">
                <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="objectUpdate.code" required readonly >
                <label for="floatingInput">Code</label>
            </div>
            <div class="form-floating mb-3">
                <input type="text" class="form-control" id="floatingInput" placeholder=""  v-model="objectUpdate.name" required>
                <label for="floatingInput">Name</label>
            </div>
            <div class="form-floating mb-3" >
                <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="objectUpdate.description" required >
                <label for="floatingInput">Description</label>
            </div>
            <div class="row">
                <div class="col-3">
                    <button class="btn btn-outline-success">Cập nhật</button>
                </div>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
  <!-- Modal detail -->
  <div class="modal fade" id="modal-detail" tabindex="-1" aria-labelledby="modal-detail" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">User of Organization</h1>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body table-responsive">
        <form @submit.prevent="addUser">
            {{objDetail.oraId}}
            <div class="list">
                <div class="form-floating mb-3">
                    <select class="form-select"  id="floatingInput" v-model="idUser">
                        <option selected>Vui lòng chọn user</option>
                        <option v-for="item in users" :key="item.userId" v-bind:value="item.userId">{{item.username}}</option>
                    </select>
                    <label for="floatingInput">User</label>
                </div>

            </div>
            <table class="table table-striped table-bordered table-hover">
            <thead>
                <th>
                    Id
                </th>
                <th>
                    organization name
                </th>
                <th>
                    user of organization
                </th>
            </thead>
            <tbody>
                <th>
                    {{objDetail.oraId}}
                </th>
                <th>
                    {{objDetail.name}}
                </th>
                <td>
                    <ul>
                        <li v-for="item in objDetail.users" :key="item">{{item.username}}</li>
                    </ul>
                </td>
            </tbody>
         </table>
         <div class="row">
            <div class="col-3">
                <button class="btn btn-outline-success">Thêm</button>
            </div>
         </div>
        </form>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button type="button" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var data = []
import axios from 'axios';
export default{
    name : "OrganizationManager",
    components : {
        
    },
    data(){
        return{
            dataList : data,
            organization : {
                code : "",
                name : "",
                orders : "",
                description : "",
                organization : {
                    oraId : ""
                },

            },checked :[],
            id : "",
            objectUpdate : {},
            objDetail : {
                oraId : "",
                name : "",
                users : []
            },
            users : [],
            idUser : '',

        }
    },
    created(){
        this.getData();
        this.getAllUser();
         axios.defaults.headers.common['Authorization'] = "Bearer "+ localStorage.getItem('token');
    },
    methods : {
        getData(){
            axios.get('/api/organization/get').then(res => { 
                    data =  res.data;
                    this.dataList = data;
            }).catch(err => {
                console.log(err);
                this.$toast.error("Permission denied");
            })
        },create(){
            console.log(this.organization);
            axios.post('/api/organization/save',this.organization).then(res => {
            console.log(res.data);
                this.getData();
                document.querySelector('#close').click();
                this.$toast.success(`Thêm mới thành công`);
            })
        },remove(id){
            axios.delete('/api/organization/delete/'+ id).then(res => {
                this.getData();
                res;
                document.querySelector('#close1').click();
                this.$toast.success(`Xóa thành công`);
            }).catch(err => {
                err;
                this.$toast.error(`Permission denied`);
            })
        },getAllListId(){
            let listCheckbox = document.querySelectorAll(".check");
            listCheckbox.forEach((item) => {
                if(item.checked){
                    this.ids.push(item.value);
                }
            })
            return this.ids;
        },checkAll(){
            var elements = document.querySelector("#check");
            var listCheckbox = document.querySelectorAll(".check");
            if(elements.checked){
                listCheckbox.forEach((item) => {
                    item.checked = true;
                })
            }else {
                listCheckbox.forEach((item) => {
                    item.checked = false;
                })
            }
        },updateById(){
            axios.put('/api/organization/update/'+ this.objectUpdate.oraId,this.objectUpdate).then(res => {
                this.getData();
                res.data;
                document.querySelector('#close2').click();
                this.$toast.success(`Cập nhật thành công`);
            }).catch(err => {
                err;
                this.$toast.error(`Permission denied`);
            })
        },
        getAllUser(){
            axios.get('/api/user/get').then(res => {
                this.users = res.data;
            })
        },
        async addUser(){
            axios.post('/api/organization/add-user/',this.objDetail,{
                params : {
                    idUser : await this.idUser
                }
            }).then(res => {
                this.getData();
                res.data;
                document.querySelector('#close3').click();
                this.$toast.success(`Thêm thành công`);
            }).catch(err => {
                err;
                this.$toast.error(`Permission denied`);
            })
        },
    }
}
</script>
<style scoped>
    table .col{
        padding-top: 1rem;
    }
    .detail{
        cursor: pointer;
    }
</style>